<template>
    <div class="box">
        <nav-bar title="语言选择" :right-click="save">
            <Button color="#37CE87">保存</Button>
        </nav-bar>
        <div class="content">
            <div @click="language = 'en'">
                <span>English</span>
                <img v-show="language === 'en'" src="@/assets/checked.png" />
            </div>
            <div @click="language = 'zh'">
                <span>中文</span>
                <img v-show="language === 'zh'" src="@/assets/checked.png" />
            </div>
        </div>
    </div>
</template>

<script>
    import { Button, Icon, Toast } from 'vant';
    import navBar from '@/components/nav-bar';

    export default {
        name: 'language',
        components: { navBar, Button },
        data() {
            return {
                language: 'zh'
            }
        },
        methods: {
            save() {
                const message = this.language === 'zh' ? '保存成功！' : 'Save Success!';

                this.$i18n.locale = this.language;
                localStorage.setItem('locale', this.language);
                Toast.success(message);
            }
        },
        created() {
            this.language = localStorage.getItem('locale') || 'zh';
        }
    }
</script>

